@import "../root.scss";

.gradient {
  width: 150px;
  height: 50px;
  @include flex-center;
  box-shadow: 0 0 10px 10px white;
  border-radius: 5px;
  position: relative;
  margin-top: 50px;

  .overlay {
    width: 144px;
    height: 44px;
    background-color: #ccc;
    border-radius: 5px;
    cursor: pointer;
    @include flex-center;
    font-size: 1rem;
    font-weight: bold;

    .text {
      position: absolute;
      width: 150px;
      height: 50px;
      color: transparent;
      background-clip: text;
      @include flex-center;
    }
  }
}

@media screen and (max-width: 1000px) {
  .gradient {
    width: 80px;
    height: 30px;
    .overlay {
      width: 76px;
      height: 26px;
      font-size: 0.75rem;
      font-weight: normal;
      .text {
        width: 80px;
        height: 30px;
      }
    }
  }
}
